import React from 'react';
import fetch from 'node-fetch';


class DashboardCard07 extends React.Component {

  constructor(props) {
    super(props);
    if(localStorage.getItem('jwt') == null){
        window.location.href = "/";
    }
    this.state = { items: [], loading: true };
}
  
componentDidMount() {
    fetch(`https://api.octorole.eliasto.me/transactions`, {
        method: 'get', 
   headers: new Headers({
     'Authorization': 'Bearer '+localStorage.getItem('jwt'), 
     'Content-Type': 'application/x-www-form-urlencoded'
   }), 
    })
      .then(res => {
        if (res.status !== 200) {
          throw new Error(`Couldn't fetch Strapi api. Status: ${res.status}`);
        }
        return res;
      })
      .then(res => res.json())
      .then(res => {
        // Successfully logged with Strapi
        // Now saving the jwt to use it for future authenticated requests to Strapi
        // Redirect to homepage after 3 sec
        this.setState(() => {
            return {
                items: res,
                loading: false,
            };
        });
      })
      .catch(err => {
        console.log(err);
      });
  }

  render(){
  return (
    <div className="col-span-full xl:col-span-12 bg-white shadow-lg rounded-sm border border-gray-200">
      <header className="px-5 py-4 border-b border-gray-100">
        <h2 className="font-semibold text-gray-800">Statistiques sur vos produits</h2>
      </header>
      <div className="p-3">

        {/* Table */}
        <div className="overflow-x-auto">
          <table className="table-auto w-full">
            {/* Table header */}
            <thead className="text-xs uppercase text-gray-400 bg-gray-50 rounded-sm">
              <tr>
                <th className="p-2">
                  <div className="font-semibold text-left">Nom</div>
                </th>
                <th className="p-2">
                  <div className="font-semibold text-center">Revenues</div>
                </th>
                <th className="p-2">
                  <div className="font-semibold text-center">Ventes</div>
                </th>
              </tr>
            </thead>
            {/* Table body */}
            <tbody className="text-sm font-medium divide-y divide-gray-100">
              {/* Row */}
              {
              
                <tr>
                <td className="p-2">
                  <div className="flex items-center">
                    <svg className="flex-shrink-0 mr-2 sm:mr-3" width="36" height="36" viewBox="0 0 36 36">
                      <circle fill="#24292E" cx="18" cy="18" r="18" />
                      <path d="M18 10.2c-4.4 0-8 3.6-8 8 0 3.5 2.3 6.5 5.5 7.6.4.1.5-.2.5-.4V24c-2.2.5-2.7-1-2.7-1-.4-.9-.9-1.2-.9-1.2-.7-.5.1-.5.1-.5.8.1 1.2.8 1.2.8.7 1.3 1.9.9 2.3.7.1-.5.3-.9.5-1.1-1.8-.2-3.6-.9-3.6-4 0-.9.3-1.6.8-2.1-.1-.2-.4-1 .1-2.1 0 0 .7-.2 2.2.8.6-.2 1.3-.3 2-.3s1.4.1 2 .3c1.5-1 2.2-.8 2.2-.8.4 1.1.2 1.9.1 2.1.5.6.8 1.3.8 2.1 0 3.1-1.9 3.7-3.7 3.9.3.4.6.9.6 1.6v2.2c0 .2.1.5.6.4 3.2-1.1 5.5-4.1 5.5-7.6-.1-4.4-3.7-8-8.1-8z" fill="#FFF" />
                    </svg>
                    <div className="text-gray-800">hey</div>
                  </div>
                </td>
                <td className="p-2">
                  <div className="text-center text-green-500">$3,877</div>
                </td>
                <td className="p-2">
                  <div className="text-center">267</div>
                </td>
              </tr>
              }
            </tbody>
          </table>
          {(this.state.loading? <p className="text-center">Chargement de vos statistiques...</p>: null)}
          {((!this.state.loading && this.state.items.length === 0)? <p className="text-center">Aucune transanctions d'enregistres.</p>: null)}


        </div>
      </div>
    </div>
  );
}
}

export default DashboardCard07;
